import { ModuleWithProviders, NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { ProductComponent } from './product/product.component';
import { EnigmaComponent } from './product/enigma/enigma.component';
import { HerculisComponent } from './product/herculis/herculis.component';
import { SwordfishComponent } from './product/swordfish/swordfish.component';
import { CasestudyComponent } from './casestudy/casestudy.component';
import { PartnersComponent } from './partners/partners.component';
import { SignupComponent } from './signup/signup.component';
import { ContactPageComponent } from './contact-page/contact-page.component';

import { SuitsComponent } from './common/suits/suits.component';
import { FeaturesComponent } from './features/features.component';
import { ServiceComponent } from './service/service.component';

export const router: Routes = [
  {
    path: '',
    redirectTo: '/',
    pathMatch: 'full'
  },
  {
    path: '',
    component: HomeComponent
  },
  // { path: '**', component: HomeComponent },
  {
    path: 'product',
    component: ProductComponent
  },

  { path: 'product/enigma', component: EnigmaComponent },

  { path: 'swordfish', component: SwordfishComponent },

  { path: 'herculis', component: HerculisComponent },

  {
    path: 'casestudy',
    component: CasestudyComponent
  },

  { path: 'partners', component: PartnersComponent },

  {
    path: 'signup',
    component: SignupComponent
  },

  {
    path: 'contact',
    component: ContactPageComponent
  },

  { path: 'suits', component: SuitsComponent },
  { path: 'features', component: FeaturesComponent },
  { path: 'services', component: ServiceComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(router, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule {}
